a{
  text-decoration: none;
  color: #333;
  &:hover{
    color: #0054d2;
  }
}
button{
   border: none;
    padding: 5px 10px;
    border-radius: 6px;
    font-weight: 500;
    background-color: #e3810c;
    color: white;
    cursor: pointer;
    transition: all 250ms ease-in;
    &:hover{
      background-color: #d57705;
      color: white!important;
    }
}